<template>
<div class="header">

  <span>{{datas.topheader[0].name}}</span>

</div>
</template>

<script>
import datas from '../../datas.json'

export default {
  name:'header',
  data(){
    return{
      datas:datas
    }
  }

}
</script>

<style lang='less' scoped>

.header{
  background:red;
  height:25px;
  line-height: 25px;
  text-align: center;
  // color:white
  span{
    font-size: 14px;

    color:white;
  }
}



</style>



