<template>
<div class="home">

    <!-- 以下是主体部分 -->
    <!-- PlaymilyHood，开发中... -->

    <div class="hoodserver1">
        <div class="hood"  @click='communitycenteropen()'>
            <div class="hoodicon">

            </div>
            <p>社区中心</p>
        </div>
        <div class="hood"  @click='sharetoys()'>
          <div class="hoodicon">

          </div>
            <p>玩具分享</p>

            <!-- 打开‘玩具分享’新页面 -->
            <!-- <transition name="wanju">
                <play-hood v-show="showtoys" @close="showtoys=false"></play-hood>
            </transition> -->

        </div>
        <div class="hood"  @click='messagebox()'>
          <div class="hoodicon">

          </div>
            <p>资讯</p>
        </div>
        <div class="hood">
          <div class="hoodicon">

          </div>
            <p>附件动态</p>
        </div>
    </div>
    <div class="hoodserver1 hoodserver2">
        <div class="hood"  @click='paradise()'>
          <div class="hoodicon">

          </div>
            <p>Playmily空间</p>
        </div>
        <div class="hood">
          <div class="hoodicon">

          </div>
            <p>会员中心</p>
        </div>
        <div class="hood">
          <div class="hoodicon">

          </div>
            <p>故事说</p>
        </div>
        <div class="hood">
          <div class="hoodicon">

          </div>
            <p>帮助</p>
        </div>
    </div>
    <!-- <div class="hoodserver2">

    </div> -->

    <!-- 以下是玩具新窗口 -->
    <transition name="wanju">
        <play-hood v-show="showtoys" @close="showtoys=false"></play-hood>
    </transition>

    <!-- 以下是玩具新窗口 -->
    <transition name="message">
        <message-hood v-show="showmessage" @close="showmessage=false"></message-hood>
    </transition>

    <!-- 以下是社区中心新窗口 -->
    <transition name="communitycenter">
        <community-center v-show="showcommunitycenter" @close="showcommunitycenter=false"></community-center>
    </transition>

    <!-- 以下是乐园新窗口 -->
    <transition name="paradise">
        <paradise-hood v-show="showparadise" @close="showparadise=false"></paradise-hood>
    </transition>




</div>
</template>

<script>
import datas from '../../../datas.json'
import playhood from '../playhood/playhood'
import communitycenters from '../communitycenter/communitycenter'
import paradise from '../paradise/paradise'
import message from '../message/message'

export default {
  data(){
    return{
      datas:datas,
      showtoys:false,
      showcommunitycenter:false,
      showparadise:false,
      showmessage:false
    }
  },
  methods:{
    sharetoys:function(){
      this.showtoys=true
    },
    communitycenteropen:function(){
      this.showcommunitycenter=true
    },
    paradise:function(){
      this.showparadise=true
    },
    messagebox:function(){
      this.showmessage=true
    }


  },
  components:{
    'play-hood':playhood,
    'community-center':communitycenters,
    'paradise-hood':paradise,
    'message-hood':message
  }
}
</script>

<style lang="less">
.wanju-enter-active,.wanju-leave-active{
        transition: all .5s;
    }
.wanju-enter,.wanju-leave-active {
        transform: translateX(100%);
        opacity: 0;
    }

.communitycenter-enter-active,.communitycenter-leave-active{
        transition: all .5s;
    }
.communitycenter-enter,.communitycenter-leave-active {
        transform: translateX(100%);
        opacity: 0;
    }
.paradise-enter-active,.paradise-leave-active{
        transition: all .5s;
    }
.paradise-enter,.paradise-leave-active {
        transform: translateX(100%);
        opacity: 0;
    }
.message-enter-active,.message-leave-active{
        transition: all .5s;
    }
.message-enter,.message-leave-active {
        transform: translateX(100%);
        opacity: 0;
    }

// 以上是每个模块的新窗口
div{
	box-sizing: border-box;
}
.home{
  margin-bottom:45px;
}
.hoodserver1{
  width:100%;
  // border:1px solid gray;
  display:flex;
  height:80px;
  text-align: center;
  .hood{
    flex:1;
    // background:#f1f1f1;
    border:1px solid #f8fff8;
    position:relative;
    p{
      font-size: 12px;
      margin-top:60px;
    }
  }
}

.hoodicon{
  width:50px;
  height:50px;
  display:block;
  // background: yellow;
  position:absolute;
  top:5px;
  left:50%;
  margin-left:-25px;
  // margin-left:50%;

}

.hoodserver1>div:nth-of-type(1) .hoodicon,.hoodserver1>div:nth-of-type(2) .hoodicon,.hoodserver1>div:nth-of-type(3) .hoodicon,.hoodserver1>div:nth-of-type(4) .hoodicon{
  background-image:url('../../../static/img/shequzhongxin2.png');
  background-position:left top;
  background-size:100% 100%;
  background-repeat:no-repeat;
}
.hoodserver1.hoodserver2>div:nth-of-type(1) .hoodicon,.hoodserver1.hoodserver2>div:nth-of-type(2) .hoodicon,.hoodserver1.hoodserver2>div:nth-of-type(3) .hoodicon,.hoodserver1.hoodserver2>div:nth-of-type(4) .hoodicon,{
  background-image:url('../../../static/img/shequzhongxin2.png');
  background-position:left top;
  background-size:100% 100%;
  background-repeat:no-repeat;
}
.hoodserver1>div:nth-of-type(2) .hoodicon{
  background-image:url('../../../static/img/wanjufenxiang2.png');
}
.hoodserver1>div:nth-of-type(3) .hoodicon{
  background-image:url('../../../static/img/zixun3.png');
}
.hoodserver1>div:nth-of-type(4) .hoodicon{
  background-image:url('../../../static/img/fujindongtai2.png');
}
.hoodserver1.hoodserver2>div:nth-of-type(1) .hoodicon{
  background-image:url('../../../static/img/leyuan1.png');
}
.hoodserver1.hoodserver2>div:nth-of-type(2) .hoodicon{
  background-image:url('../../../static/img/huiyuanzhongxin.png');
}
.hoodserver1.hoodserver2>div:nth-of-type(3) .hoodicon{
  background-image:url('../../../static/img/gushishuo.png');
}
.hoodserver1.hoodserver2>div:nth-of-type(4) .hoodicon{
  background-image:url('../../../static/img/bangzhu.png');
}


</style>



