    <p>如果你有更好的建议，请联系我</p>
    <!-- <p>这是一个新窗口</p> -->


  </div>
</template>

<script>
import datas from '../../../datas.json'

export default {
  name:'playtoys',

  methods:{
    closeDetails:function(){
      this.$emit('close')
    }
  }
}

</script>

<style lang="less" scoped>

div{
	box-sizing: border-box;
}

.playtoys{
  width:100%;
  height:100%;
  position:fixed;
  top:0px;
  left:0px;
  background:white;
  z-index: 31;
  text-align: center;
  .spantop{
    position:absolute;
    left:10px;
    top:10px;
    width:25px;
    height:25px;
    text-align: center;
    line-height: 25px;;
    display:block;
    background: white;
    color:white;
    z-index: 81;
    color:black;
    border-radius: 50%;
    font-size: 20px;
  }
  p{
      font-size: 12px;
      margin-top:20px;
  }
}
// span{
