<template>
<div class='stating'>

  <!-- 以下是个人动态，包括发布的项目，喜欢、收藏和支持的项目 -->
  <!-- <p>12正在开234发中..</p> -->

  <div class="statebox" v-for = 'item in datas.static'>

      <h3>我分享了玩具<i>{{item.toys}}</i>给<em>{{item.sharetouser}}</em>:<span>{{item.daytime}}天前</span></h3>
      <div class='detailtext'>{{item.detailmsg}}</div>
      <div class="imgs">
          <div class="mainimgs">
            <img :src="item.imgs1" alt="">
          </div>
          <div class="mainimgs">
            <img :src="item.imgs2" alt="">
          </div>
          <div class="mainimgs">
            <img :src="item.imgs3" alt="">
          </div>

      </div>
      <div class="zan">
        <span @click='clicklove1' :style="spanimgpic"></span>(212)
        <i @click='clicklove2' :style="iimgpic"></i>(65)
        <em @click='clicklove3'></em>我要评论
      </div>
      <div class="comments">
          <h2>用户评论:</h2>
          <div class="commentsmsg" v-for='items in item.allcomments'>
            <p>{{items.user}}<span>{{items.comments}}</span></p>
          </div>

      </div>

  </div>


</div>
</template>
import datas from '../../../datas.json'

<script>
import datas from '../../../datas.json'

export default {
  data(){
    return{
      datas:datas,
      spanimgpic:"backgroundImage:url('../../../static/img/staticlove1.png')",
      spanimgpicchange:"backgroundImage:url('../../../static/img/staticlove2.png')",
      iimgpic:"backgroundImage:url('../../../static/img/sharelove1.png')",
      iimgpicchange:"backgroundImage:url('../../../static/img/sharelove2.png')",

    }
  },
  methods:{
    // 点击后，爱心变红，改变文件地址的路径
    clicklove1:function(){
      this.spanimgpic = this.spanimgpicchange;
      alert('收藏成功')
    },
    clicklove2:function(){
      this.iimgpic = this.iimgpicchange;
      alert('分享模块尚未开通..')
    },
    clicklove3:function(){
      alert('评论模块尚未开通..')
    }


  },
  mounted(){



  }
}

</script>



<style lang="less">
div{
	box-sizing: border-box;
}
.stating{
  margin-bottom:45px;
  font-size: 12px;
  padding:12px;
}
.statebox{
  width:100%;
  height:auto;
  border-radius: 2px;
  // background:skyblue;
  padding:6px;
  padding-top:20px;
  border:1px solid #ccc;
  margin-bottom:20px;
  h3{
    font-size: 12px;
    height:20px;
    line-height: 20px;
    margin-bottom:12px;
    i{
      color:red;
      margin-left:5px;
      margin-right:5px;
    }
    em{
      color:#2d4a58;
      margin-left:5px;
      margin-right:5px;
    }
    span{
      font-size: 10px;
      float:right;
      margin-right:12px;

    }
  }
  .detailtext{
    height:auto;
    font-size: 13px;
    background: white;
    padding:5px 0px 5px 0px;
    line-height: 18px;
    font-weight: bold;
    color:#34352c;
    font-family: "Arial","Microsoft YaHei","黑体","宋体",sans-serif;
    // font: 14px/1 Tahoma,Helvetica,Arial,"\5b8b\4f53",sans-serif;
  }
  .imgs{
    width:100%;
    height:105px;
    display:flex;
    margin-bottom:12px;
    // background:yellow;
    .mainimgs{
      flex:1;
      width:100px;
      height:100px;
      // border:1px solid red;
      margin-left:5px;
      margin-right:5px;
      img{
        width:100%;
        height:100%;
      }
    }
  }
  .zan{
    font-size: 16px;
    color:#34352c;
    span{
      width:20px;
      height:20px;
      display:inline-block;
      background-image:url('../../../static/img/staticlove1.png');
      background-position:left top;
      background-size:20px 20px;
      vertical-align: middle;
        margin-right:3px;
    }
    i{
      width:20px;
      height:20px;
      display:inline-block;
      background-image:url('../../../static/img/sharelove1.png');
      background-position:left top;
      background-size:20px 20px;
      vertical-align: middle;
      margin-left:20px;
      margin-right:3px;
    }
    em{
      width:20px;
      height:20px;
      display:inline-block;
      background-image:url('../../../static/img/cms.png');
      background-position:left top;
      background-size:20px 20px;
      vertical-align: middle;
      margin-left:20px;
      margin-right:3px;
    }


  }
  .comments{
    margin-top:12px;
    height:auto;
    padding:12px;
    padding-bottom:6px;
    h2{
      font-size: 16px;
      font-weight: bold;
    }
    .commentsmsg{
      width:100%;
      height:auto;
      margin-top:12px;
      font-weight: bold;
      color:#2d4a58;
      line-height: 14px;
      span{
        color:#a2b4ba;
        margin-left:6px;
        font-weight: 100;
      }

    }


  }


}



</style>



