<template>
<div class="family">

    <div class="pics">
        <!-- 关注我 -->
        <div class="focusme" @click='clickme()'>
          <span></span>关注我
        </div>
        <img src="http://img.pconline.com.cn/images/upload/upc/tx/photoblog/1309/21/c102/26036241_26036241_1379735014356_mthumb.jpg" alt="">
        <!-- 覆盖文字层 -->
          <div class="myspace">
              <p>小李子的空间<span>987人浏览</span></p>
          </div>
    </div>

    <!-- 以下是主体部分 -->
        <ul>
            <li><router-link to="/family/state">我的动态</router-link></li>
            <li><router-link to="/family/home">Playmily家园</router-link></li>
            <li><router-link to="/family/contact">联系我</router-link></li>
        </ul>

        <!-- 以下是配置不同方面的路由 -->
        <router-view></router-view>

</div>
</template>

<script>
import datas from '../../../datas.json'



export default {
  name:'family',
  data(){
    return{
      datas:datas
    }
  },
  methods:{
    clickme:function(){
      $('.focusme').html('<em>已关注</em>')
    }
  }

}
</script>

<style lang='less' scoped>
div{
	box-sizing: border-box;
}
.pics{
  width:100%;
  height:190px;
  // background: white;
  position:relative;
  img{
    width:100%;
    height:190px;
    position: absolute;
    left:left;
    top:0px;
    z-index: 8;
  }
  .focusme{
    position: absolute;
    right:10px;
    top:10px;
    z-index: 23;
    width:60px;
    height:18px;
    line-height: 18px;
    font-size: 13px;
    // background: white;
    color:white;
    // text-align: center;
    span{
      width:20px;
      height:20px;
      display:block;
      position:absolute;
      left:-21px;
      top:-3px;
      background-image:url('../../../static/img/calltome.png');
      background-position:left top;
      background-size:100% 100%;

    }
  }
  .myspace{
    width:100%;
    height:40px;
    line-height: 40px;
    position:absolute;
    bottom:0px;
    left:0px;
    z-index: 12;
    background: rgba(0,0,0,0.1);
    color:white;
    padding-left:20px;
    span{
      float:right;
      font-size: 12px;
      margin-right:20px;
    }
  }
}

li{
  list-style-type:none;
}
ul{
  width:100%;
  height:30px;
  display:flex;
  font-size: 13px;
  border-bottom:1px solid #a7c8db;
  // margin-bottom:2px;
  li{
    height:30px;
    line-height: 30px;
    flex:1;
    text-align: center;
    a{
      color:black;
      text-decoration: none;
      &.router-link-active {
                  color : red;
                  font-weight: bold;
              }
    }
  }
}


</style>



