import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import release from '@/components/release/release'
import square from '@/components/square/square'
import family from '@/components/family/family'
import squaring from '@/components/squaring/squaring'
import mynews from '@/components/mynews/mynews'

import state from '@/components/state/state'
import contact from '@/components/contact/contact'
import home from '@/components/home/home'
import callmsg from '@/components/callmsg/callmsg'
import login from '@/components/login/login'
import usercenter from '@/components/usercenter/usercenter'
import aboutme from '@/components/aboutme/aboutme'
import helps from '@/components/helps/helps'
import writeaboutshare from '@/components/writeaboutshare/writeaboutshare'
import writefeed from '@/components/writefeed/writefeed'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/release',
      component: release //发布任务
    },
    {
      path: '/square',
      component: square ,//广场
      children:[
        {
          path:'/square/squaring',
          component:squaring,
          children:[
            {
              path:'/square/squaring/callmsg',
              component: callmsg
            }
          ]
        },
        {
          path:'/square/mynews',
          component:mynews
        },
        {
          path:'/square',
          redirect:'/square/squaring'
        }
      ]
    },
    {
        path: '/family',
        component:family,
        children:[
          {
            path:'/family/state',
            component:state
          },
          {
            path:'/family/home',
            component:home
          },
          {
            path:'/family/contact',
            component:contact
          },
          {
            path:'/family',
            redirect:'/family/state'
          }
        ]
    },
    {
        path : '/',
        redirect : '/release'
    },
    {
        path : '/login',
        component:login
    },
    {
        path : '/usercenter',
        component:usercenter
    },
    {
        path : '/aboutme',
        component:aboutme
    },
    {
        path : '/helps',
        component:helps
    },
    {
        path : '/writeaboutshare',
        component:writeaboutshare
    },
    {
        path : '/writefeed',
        component:writefeed
    }
  ]
})



