<template>
  <div class="paradise">
    <span @click="closeDetails" class='spantop'>&lt;</span>
    <!-- <p>这是一个新窗口</p> -->
    <div class="content">

      <!-- 以下是使用数据渲染的模块 -->
      <div class="playnews" v-for='(item,index) in datas.playhood'>
          <h5><i>{{item.name}}</i>发布了说说:<span>{{item.time}}天前</span></h5>
          <p class='playtext'>{{item.text}}</p>
          <div class="pictures">
            <div class="detailimg">
              <img :src="item.legaopic01" alt="">
            </div>
            <div class="detailimg">
              <img :src="item.legaopic02" alt="">
            </div>
            <div class="detailimg">
              <img :src="item.legaopic03" alt="">
            </div>
              <!-- <img :src="item.legaopic01" alt="">
              <img :src="item.legaopic02" alt="">
              <img :src="item.legaopic03" alt=""> -->
          </div>
              <p class='viewnumber'>浏览{{item.viewnum}}人<span class='goodtext' @click.once='clicktoadd(index)'>{{item.best}}</span><span>点赞</span></p>
      </div>


    </div>

  </div>
</template>

<script>
import datas from '../../../datas.json'

export default {
  name:'paradise',
  data(){
    return{
      datas:datas,
      num:true
    }
  },
  methods:{
    closeDetails:function(){
      this.$emit('close')
    },
    clicktoadd:function(index){
      console.info(index);
      var goodtext=$('.content>div:eq('+(index)+')').find('.goodtext').html();
      console.info(goodtext);
      var addnum = ++goodtext;
      $('.content>div:eq('+(index)+')').find('.goodtext').html(addnum);
    }
  },
  computed:{
      // 可以用来缓存
  }
}

</script>

<style lang="less" scoped>

div{
	box-sizing: border-box;
}

.paradise{
  width:100%;
  height:100%;
  position:fixed;
  top:0px;
  left:0px;
  background:white;
  z-index: 31;
  padding:20px;
  overflow:scroll;
  .spantop{
    position:absolute;
    left:10px;
    top:10px;
    width:25px;
    height:25px;
    text-align: center;
    line-height: 25px;;
    display:block;
    background: white;
    color:white;
    z-index: 81;
    color:black;
    border-radius: 50%;
    font-size: 20px;
  }
  .content{
    width:100%;
    height:100%;
    background: white;
    margin-top:10px;
    // margin-bottom:100px;
  }

}

.content{
  .playnews{
    width:100%;
    height:auto;
    margin-bottom:20px;
    // height:259px;
    // background: yellow;
    background: rgba(255,222,201,0);
    border-bottom:1px solid #bfdbb8;
    padding:0px 5px 5px 5px;
    h5{
      font-size: 12px;
      padding:12px 0 12px 0;
      font-weight: bold;
      i{
        color:#00b0f0;
        margin-right:5px;
      }
      span{
        float:right;
        margin-right:12px;
        color:gray;
        font-weight: 200;
      }
    }
    .playtext{
      font-size: 12px;
      padding:2px 0 12px 0;
      line-height: 15px;
    }
    .pictures{
      width:100%;
      height:105px;
      display:flex;
      // background: yellow;
      .detailimg{
        width:102px;
        height:102px;
        flex:1;
        margin-left:5px;
        margin-right:5px;
        img{
          width:100%;
          height:100px;
        }
      }
    }
    .viewnumber{
      font-size: 12px;
      padding:12px 0 12px 0;
      span{
        float:right;
        margin-right:2px;
      }
    }

  }


}





</style>



