<template>
  <div class="writeshares">

    <!-- 这是关闭当前登录页 -->
    <router-link to="/release"><span class='closebtn iconfont'>&#xe647;</span></router-link>

    <div class="sharebox">
        <form class="" action="" method="post">
            <dd><span>共享项目名称:</span><input  required="required" type="text" name="" value="">  </dd>
            <dd class='gotofile'><span>上传封面图:</span><input class='updatefile' type="file" required="required" name="" value="">  </dd>
            <dd><span>项目介绍:</span><textarea maxlength='130'  required="required" placeholder="请输入项目内容(控制在130字以内)"></textarea> </dd>
            <dd><span>发起人:</span><input type="text" name=""  required="required" value="">  </dd>
            <dd><span>发起愿景:</span><input type="text" name=""  required="required" value="">  </dd>
            <dd><span>社区名称:</span><input type="text" name="" required="required"  value="">  </dd>
            <dd><span>共享目标:</span><input type="text"  required="required" name="" value="">  </dd>
            <dd><input class='subtm' type="submit" name="" value="完成提交">  </dd>

        </form>
    </div>


  </div>
</template>

<script>


export default {


}
</script>

<style lang="less" scoped>
div{
	box-sizing: border-box;
}


.writeshares{
  background:white;
  width: 100%;
  height:100%;
  position: fixed;
  overflow:hidden;
  left :0;
  top :0;
  font-size: 12px;
  z-index:109;
}
.closebtn{
    width:28px;
    height:28px;
    text-align: center;
    line-height: 28px;
    border-radius: 50%;
    display:block;
    color:white;
    background: rgba(0,0,0,.5);
    position:fixed;
    top:6px;
    left:6px;
    z-index: 20;
  }
.sharebox{
  width:100%;
  height:100%;
  padding:20px 5px 0px 20px;

  overflow: scroll;
  dd.gotofile{
    position:relative;
  }
  dd{
    width:100%;
    heigth:60px;
    // background: yellow;
    margin-top:20px;

    input{
      margin-left:12px;
    }
    input.updatefile{
      color:red;
      position:absolute;
      left:100px;
      top:-8px;
      // margin-right:12px;
      border-radius: 4px;
      padding: 4px 12px;
      overflow: hidden;
      color: #1E88C7;
      text-decoration: none;
      text-indent: 0;
      line-height: 20px;
      // background: #D0EEFF;

    }
    input.subtm{
      margin-left:123px;
      font-weight: bold;
      border:none;
      padding:5px;
      background: #495a80;
      color:white;
    }
    textarea{
      margin-left:12px;
      vertical-align: top;
      height:100px;
    }
    span{
      display:inline-block;
      width:110px;
      // background: red;
      text-align: right;
      font-size: 15px;
    }
  }
}

</style>



