<template>
<div class="squre">
  <!-- 以下是主体内容部分 -->

  <div class="content">


      <!-- 以下是一个具体的消息对话模块 -->
       <!-- @click='appendmsg(item)'  :items='currentmsgbox' -->
      <div class="cbox" v-for='item in datas.community' @click='appendmsg(item)' >

        <router-link to="/square/squaring/callmsg">
          <div class="cimg">
            <!-- <p>群联</p> -->
            <!-- <img src="../../../static/img/chat.png" alt=""> -->
            <img :src="item.pictures" alt="">
          </div>
          <div class="ctext">
            <p><span>距离你的社区{{item.squaring.distancing}}m</span></p>
            <p><span>{{item.squaring.communityname}}</span></p>
            <p><span>[群员]{{item.squaring.peoplenum}}人</span></p>
            <p><span>【{{item.squaring.textpeople}}】：{{item.squaring.information}}</span></p>
            <p><span>{{item.squaring.noread}}条未读</span></p>
          </div>

          <!-- <my-msg :getmsg='item'></my-msg> -->

          </router-link>
      </div>




  <router-view></router-view>

  </div>

  <!-- <transition name="newmsgbox">
      <modul-msgbox v-show="showmsgbox" :item="currentmsgbox" @close="showmsgbox=false"></modul-msgbox>
  </transition> -->


</div>
</template>

<script>
import datas from '../../../datas.json'
// import callmsg from '../callmsg/callmsg'
import mymsg from '../mymsg/mymsg'


export default {
  name:'squre',
  data(){
    return{
      datas:datas,
      showmsgbox:false,
      currentmsgbox:{}
    }
  },
  methods:{
    // 传递数据给子组件
    appendmsg:function(item){
      // this.showmsgbox = true;
        this.currentmsgbox = item;
        console.info(item.project)
        // console.info(getmsg+'---')
    }
  },
  components : {
      'my-msg':mymsg,
      // props:{
      //   getmsg:Object
      // }
  },

}
</script>

<style lang='less' scoped>

// .newmsgbox-enter-active,.newmsgbox-leave-active{
//     transition: all .5s;
// }
// .newmsgbox-enter,.newmsgbox-leave-active {
//     transform: translateX(100%);
//     opacity: 0;
// }


div{
	box-sizing: border-box;
}
.content{
  margin-bottom:45px;
  // margin-top:45px;
}
.cbox>a{
  color:black;
}

.cbox{
  width:100%;
  height:111px;
  border-top:1px solid #fff8f2;
  border-bottom:1px solid #fff8f2;
  margin-bottom:12px;
  font-size: 10px;
  .cimg{
    width:25%;
    height:100%;
    float:left;
    text-align: center;
    img{
      width:75px;
      height:75px;
      margin-top:8px;
    }
  }
  .ctext{
    width:75%;
    height:100%;
    float:left;
    // background: red;
    p:nth-of-type(1){
      // font-size: 10px;
      color:#e26013;
      height:18px;
      line-height: 18px;
      // background: yellow;
      // diplay:block;
      span{
        display:inline-block;
        float:right;
        margin-right:15px;
      }
    }
    p:nth-of-type(2){
      font-size: 18px;
      margin-top:5px;
      text-overflow:ellipsis;
      overflow: hidden;
      white-space: nowrap;
      // color:#94a8a4;
    }
    p:nth-of-type(3){
      font-size: 10px;
      margin-top:6px;
      // color:#94a8a4;
    }
    p:nth-of-type(4){
      font-size: 12px;
      color:#666666;
      margin-top:6px;
      text-overflow:ellipsis;
      overflow: hidden;
      white-space: nowrap;
    }
    p:nth-of-type(5){
      font-size: 10px;
      // color:red;
      margin-top:8px;
      text-align: center;
      span{
        height:15px;
        line-height: 15px;
        width:45px;
        display:inline-block;
        background: red;
        color:white;
        border-radius: 3px;
        float:right;
        margin-right:12px;
      }
    }


  }

.ctext p{

}




}










</style>



