    <!-- 以下是文章的具体内容 -->
    <div class="texts">
      <h2>我所了解的LEGO乐高系列玩具</h2>
      <p>Smzdm上推送频率最高和推送数量最多的玩具，肯定而且必须是lego乐高了，我也经常在站里收到各种咨询乐高相关问题的@，尽自己所知的答复大家，一直都想写一篇关于乐高全系列介绍文章，但是在翻阅各种资料后悲观的发现，对于我来说，这是个不可能完成的任务。原因有二：其一，很多系列我连见都没见过怎么介绍？其二，要想独立完成乐高全系列介绍贴这个基本不可能的任务，文字篇幅一定会很长，长到我会写到吐，长到你会回复说“太长了懒得看纯支持一下”。所以我只能站在自己的角度，尽可能客观的来对乐高系列玩具进行1、2句话的点评，让喜欢乐高却感觉无从下手的朋友看了，能稍微有点帮助，这就是本意了。
</p>
    <img src="../../../static/img/legao01.jpg" alt="">
    <p>首先，建议有意于购买乐高或者是想继续购买乐高却不知如何下手的朋友先阅读下《乐高全书》中文版（数据更新至2009年）或者英文版（数据更新至2012年），当然也可以选择登录这个拥有非常全面的乐高数据库网站免费浏览，相信你会在惊讶原来乐高有如此多的系列之余，瞬间理解为什么前面说的“不可能完成的任务”这句话不是我在矫情。罗列一下数据吧： 2012年，乐高共计推出了32个系列706款积木，是有史以来数量最多的一年，2013年有380款乐高产品上市……每个人的兴趣爱好不同，也没有规定男生就一定不能玩女孩系列的乐高，也没有规定科技系列的受众一定不包括女生，你可以完全不知道星球大战是什么但是因为非常喜欢谢耳朵然后买了一个死星。所以第一个问题的答案就是：选你喜欢的而且你买得起的那款或者那些款乐高。这个答案还有一个隐藏属性：当一款乐高玩具低于5折哪怕你不是那么喜欢，也建议你毫不犹豫下单。</p>
    <p>玩具反斗城在国内有16家分店，具体分布可以自行查询，之前的魔咒“玩具反斗城不会在北方开店”已经随着北京店的开业被打破了，玩具反斗城的优点是新货比较全，而且办理会员卡后再赶上促销活动，可以用很低的折扣买到好货。缺点是想买到1、2年前出的乐高都比较困难。值得注意的是，玩具反斗城有山寨店。</p>
    <p>香港油麻地地铁站B出口马路对面的那些小店，不夸张的说能买到你想买到的任何一款乐高，而沙田新城市广场的玩具反斗城隔壁有家儿童书店也值得一去，里面的乐高相关的书籍非常齐全。</p>
    <p>万能的淘宝、京东、亚马逊和当当也是购买乐高的好去处，淘宝适合定向购买，因为淘宝货全而且价格相对便宜，乐高是没有假货的，但是要特别注意的是骗子很多，当一个售价1000元的乐高，卖家说只需200元或者更低，千万不要买，低于官方指导价5折的情况很少见，在价格差不多的前提下，尽量在天猫店或者卖家信誉高的店铺购买。还有一种情况是“杀肉”，就是把某款乐高拆开卖，有人买砖，有人买人仔，各得其所。而京东、亚马逊和当当这样的电商只有在促销的时候才值得去买，还记得大明湖畔那座259元的灯塔吗？</p>
    <img src="../../../static/img/legao02.jpg" alt="">
    <p>City城市系列：作为乐高玩具系统的设定，还有什么比一个城市更包罗万象的呢？City城市系列包括但是不仅仅是只有JC总部（7498），消防局（60004），飞机场（3182，3368），火车站（7939），医院（4429），码头（4644，4645），工地（7633），矿山（4204），以及各种车（4431，4432）、飞机（4439，60010）和船（60005）等等......打造属于你的乐高城市需要满足2个条件，足够多的金钱和足够大的空间，当然这2个条件同样适用于乐高其他系列。City城市系列值得购买的包括但不限于上述括号内标注的编号，后续的其他系列介绍也是基于这样的设定，不再赘述。我所了解的LEGO乐高系列玩具</p>
    <img src="../../../static/img/legao03.jpg" alt="">
    <p>科技系列：科技系列造型科技感十足，并且运用了齿轮组、马达和遥控等机械部件，使乐高能够真正的动起来，因此可玩性更高，当然价格相对其他系列略贵些。科技系列也是大坑，准备入科技坑的可以先买科技砖书研究下科技系列无重点推荐，全是好货。我所了解的LEGO乐高系列玩具</p>
    <p>来源：什么值得买</p>
