<template>
<div class="squre">

  <div class="topnav">
      <ul class="nav">
          <li><router-link to="/square/squaring">群联</router-link></li>
          <li><router-link to="/square/mynews">我的消息</router-link></li>
      </ul>
  </div>
  <router-view></router-view>


</div>
</template>

<script>
import datas from '../../../datas.json'



export default {
  name:'squre',
  data(){
    return{
      datas:datas
    }
  }

}
</script>

<style lang='less' scoped>
div{
	box-sizing: border-box;
}
.topnav{
  width:100%;
  height:50px;
  background:#495a80;
  position: relative;
  ul{
    overflow: hidden;
    text-align: center;
    border:1px solid white;
    // background: white;
    width:161px;
    height:25px;
    line-height: 25px;
    display:block;
    position:absolute;
    left:50%;
    top:10px;
    margin-left:-90px;
    border-radius: 5px;
    li{
      width:80px;
      height:25px;
      float:left;
      a{
          color:white;
          text-decoration: none;
          &.router-link-active {
                      color : yellow;
                  }
      }

    }
  }
}
.topnav ul{
  li:nth-of-type(1){
    border-right:1px solid white;
    // background: yellow;
  }
  li:nth-of-type(2){
    // border-right:1px solid white;
    // background: yellow;
  }
}


</style>



