<template>
  <div class="loginandout">

    <!-- 这是关闭当前登录页 -->
    <router-link to="/release"><span class='closebtn iconfont'>&#xe647;</span></router-link>

    <!-- 以下是具体登录组件内容 -->
    <div class='logotext'>
      <p>Playmilyhood</p>
      <p>共享一个玩具，收获两种快乐童年！</p>
    </div>
    <div class="loginbox">
      <form class="" action="/release" method="post">
        <dl>
          <dd>登录:<input type="text" value="" required="required"> </dd>
          <dd>注册:<input type="password" value="" required="required"> </dd>
          <dd class='gotobtn'><input class='subtn' type="submit" value="确定" @click.stop='linktousercenter()'></dd>
          <!-- <dd><input class='resetbtn' type="reset" value="重置"> </dd> -->
        </dl>
      </form>
    </div>



  </div>
</template>

<script>


export default {

  methods:{
    linktousercenter:function(e){
      // e.preventDefault;
        // alert('用户中心模块正在建设中..')
    }
  }
}
</script>

<style lang="less" scoped>
div,input{
	box-sizing: border-box;
}


.loginandout{
  background:white;
  width: 100%;
  height:100%;
  position: fixed;
  overflow:hidden;
  left :0;
  top :0;
  font-size: 12px;
  z-index:109;
}
.closebtn{
    width:28px;
    height:28px;
    text-align: center;
    line-height: 28px;
    border-radius: 50%;
    display:block;
    color:white;
    background: rgba(0,0,0,.5);
    position:fixed;
    top:6px;
    left:6px;
    z-index: 20;
  }
.logotext{
  width:100%;
  height:50px;
  margin-top:50px;
  // border:1px solid red;
  text-align:center;
  p{
    margin-top:12px;
  }

}
.loginbox{
    width:300px;
    height:200px;
    border:1px solid #ccc;
    margin:0 auto;
    margin-top:0px;
    padding:20px;
    dd{
      // background:yellow;
      margin-top:20px;
      border-bottom:1px solid #ccc;
      input{
        padding-left:30px;
        width:210px;
        outline:none;
        border:none;
      }
      input.subtn{
        padding-left:0;
        width:75px;
        margin-left:89px;
        background: white;
        border-bottom:1px solid black;
      }

    }
    dd.gotobtn{
      border-bottom:none;
    }



}




</style>



