<template>
<div class="moddetail" id='wrapper'>
  <!-- 关闭页面 -->
  <span class='closebtn iconfont' @click="closeDetail()">&#xe647;</span>

  <!-- <p>{{item.originator}}</p> -->
  <div class="sharenames">
    <p>玩具共享 / {{item.project}}</p>
  </div>
  <div class="pics">
<img :src="item.Shared.pics" alt="">
  </div>

  <div class="details">
<p><i class='iconfont'>&#xe6b1;</i>【发起共享·项目介绍】:{{item.details}}</p>
  </div>

  <div class="founded">
<p><i class='iconfont'>&#xe631;</i>发起人:{{item.Shared.founded}}</p>
  </div>
  <div class="vision">
<p><i class='iconfont'>&#xe62f;</i>发起愿景:{{item.Shared.vision}}</p>
  </div>
  <div class="ad">
<p><i class='iconfont'>&#xf006a;</i>地理位置:{{item.Shared.ad}}</p>
  </div>
  <div class="housein">
<p><i class='iconfont'>&#xe627;</i>小区名称:{{item.Shared.housein}}</p>
  </div>
  <div class="target">
<p><i class='iconfont'>&#xe618;</i>众筹目标:{{item.Shared.target}}</p>
  </div>
  <div class="sharebtns">
<span @click='clicklink1()'>
  点击参与共享
</span>
<!-- 这是弹出的微信群部分 -->
  <div class="weixin" @click='clicklink2()'>
  </div>

  </div>

  <div class="doneornot">
      <div class="done1 doning">
          <p>完成进度</p>

      </div>
      <div class="done2 doning">
          <p class='doneproducts'>物品要求</p>
          <p>场所空间</p>
          <p>玩具共享情况</p>
          <p>资金需求</p>
      </div>
      <div class="done3 doning">
          <p class='doneaction'>完成情况</p>
          <p><span>{{item.Shared.place}}</span>人提供</p>
          <p><span>{{item.Shared.playobj}}</span>人分享</p>
          <p><span>{{item.Shared.money}}</span>人支持</p>
      </div>

  </div>


</div>
</template>


<script>
// import datas from '../../../datas.json'

export default {
  // name:'moddetail',
  props : {
      item : {
          type : Object,
          required : true
      }
  },
  methods:{
    closeDetail:function(){
      this.$emit('close');
    },
    clicklink1:function(){
      // $('.weixin').show(3000);
      // $('.weixin').animate{('display','block'),2000}
      $('.weixin').css('display','block')
    },
    clicklink2:function(){
      // $('.weixin').hide(3000);
      $('.weixin').css('display','none')
    },
    mounted(){ //组件已经挂载好，可以进行DOM操作
        //实现滚动效果
        // this.listScroll = new IScroll(".wrapper");

    }


  }
  // data(){
  //   return{
  //     datas:datas
  //   }
  // }
}
</script>

<style lang="less" scoped>

div{
	box-sizing: border-box;
}

@font-face {
  font-family: 'iconfont';
  src: url('../../../static/font/iconfont.eot');
  src: url('../../../static/font/iconfont.eot?#iefix') format('embedded-opentype'),
  url('../../../static/font/iconfont.woff') format('woff'),
  url('../../../static/font/iconfont.ttf') format('truetype'),
  url('../../../static/font/iconfont.svg#iconfont') format('svg');
}
.iconfont{
  font-family:"iconfont" !important;
  font-size:14px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}

.closebtn{
    width:28px;
    height:28px;
    text-align: center;
    line-height: 28px;
    border-radius: 50%;
    display:block;
    color:white;
    background: rgba(0,0,0,.5);
    position:fixed;
    top:6px;
    left:6px;
  }

.weixin{
  width:180px;
  height:180px;
  position:absolute;
  left:100px;
  top:230px;
  background-image:url('../../../static/img/linktome.jpg');
  background-position: left top;
  background-size:100% 100%;
  display:none;
}
.moddetail{
    overflow-y:scroll;
    width: 100%;
    height:100%;
    background:#f2f2f2;
    position: fixed;
    left :0;
    top :0;
    z-index:99;
  }
.sharenames{
  height:50px;
  line-height: 20px;
  width:100%;
  background: #f17c67;
  color:white;
  padding:12px 15px 50px 40px;
  margin-bottom:12px;
}
.pics{
  width:100%;
  height:170px;
  // background: #495a80;
  padding:3px;
}
.pics img{
  width:100%;
  height:100%;
  border-radius: 2px;
}
.details{
  width:100%;
  height:auto;
  font-size: 12px;
  margin-top:15px;
  margin-bottom:15px;
  padding:12px 3px 12px 12px;
  // border-top:1px solid #e8ecf5;
  border-top:1px solid #e8ecf5;
  border-bottom:1px solid #e8ecf5;
  color:#545051;
  background: #f7fef7;
}

.founded{
  width:100%;
  height:25px;
  font-size: 12px;
  line-height: 25px;
  // border:1px solid gray;
  padding-left:12px;
  border-top:1px solid #e8ecf5;
  border-bottom:1px solid #e8ecf5;
  color:#9ea09f;
}

.vision{
  width:100%;
  height:auto;
  font-size: 12px;
  padding:3px 3px 3px 12px;
  // border-top:1px solid #e8ecf5;
  border-bottom:1px solid #e8ecf5;
  color:#9ea09f;
}
.ad{
  width:100%;
  height:auto;
  font-size: 12px;
  margin-top:15px;
  padding:6px 3px 6px 12px;
  // border-top:1px solid #e8ecf5;
  border-bottom:1px solid #e8ecf5;
  color:#9ea09f;
}

.housein{
  width:100%;
  height:auto;
  font-size: 12px;
  padding:6px 3px 6px 12px;
  // border-top:1px solid #e8ecf5;
  border-bottom:1px solid #e8ecf5;
  color:#9ea09f;
}
.target{
  width:100%;
  height:auto;
  font-size: 12px;
  margin-top:15px;
  padding:6px 3px 6px 12px;
  // border-top:1px solid #e8ecf5;
  border-bottom:1px solid #e8ecf5;
  color:red;
}
.sharebtns{
  width:100%;
  height:70px;
  font-size: 12px;
  // margin-top:15px;
  background: #f7fef7;
  text-align: center;
  padding-top:20px;
  padding-left:50%;
  span{
    width:120px;
    height:30px;
    line-height:30px;
    display:block;
    background: red;
    border-radius: 16px;
    color:white;
    font-size: 16px;
    margin-left:-60px;
  }
}
.doneornot{
  width:100%;
  height:130px;
  font-size: 12px;
  // margin-top:15px;
  background: white;
  display:flex;
  text-align: center;
  .doning{
    flex:1;
    // background: yellow;
  }
}

.doneornot{
  // background: #fdb8a8;
  background:url('../../../static/img/wenli.png');
  padding-top:20px;
  padding-bottom:10px;
  .done1{
    border-right:1px solid gray;
    p{
      margin-top:30px;
      font-size: 20px;
    }
  }
  .done2{
    background: white;
    border-right:1px solid gray;
    .doneproducts{
      border-bottom:1px dashed gray;
      margin-bottom:12px;
      padding-bottom:6px;
    }
    p{
      margin:6px 0px 6px 0px;
    }
  }
  .done3{
    background: #ffffff;
    border-right:1px solid gray;
    .doneaction{
      border-bottom:1px dashed gray;
      margin-bottom:12px;
      padding-bottom:6px;
    }
    p{
      margin:6px 0px 6px 0px;
      span{
        color:red;
      }
    }
  }
}


</style>



