<template>
  <div class="msgbox">
    <!-- 以下是对话框的具体内容 -->
    <div class="msgtalking">
        <!-- 以下是关闭按钮 -->
        <router-link to="/square"><span class='closebtn iconfont'>&#xe647;</span></router-link>

        <!-- 以下是对话内容 -->
        <div class="liao">
            <p>-群聊-</p>
        </div>
        <div class="main">
            <p class='topp'>欢迎来到共享玩具社区乐园！</p>
              <p>通信模块正在建设中...</p>
        </div>
        <div class="addtalk">

            <input class='talktext' type="text"value="" placeholder="输入对话">
            <input  class='talksubmit' type="submit" name="" value="提交">
        </div>




        <!-- 组件通信部分,对上一级路由的数据进行绑定 -->
        <!-- <my-msg></my-msg> -->
    </div>

  </div>
</template>

<script>
import mymsg from '../mymsg/mymsg'

export default {
  data(){
    return{
      // showcallmsg:false
    }
  },
  methods:{
    // closeDetail:function(){
    //   this.showcallmsg = false;
    // },
  },
  components:{
    'my-msg':mymsg
  }

}
</script>

<style lang="less" scoped>

div{
	box-sizing: border-box;
}

.closebtn{
    width:28px;
    height:28px;
    text-align: center;
    line-height: 28px;
    border-radius: 50%;
    display:block;
    color:white;
    background: rgba(0,0,0,.5);
    position:fixed;
    top:6px;
    left:6px;
    z-index: 20;
  }

.msgbox{
  background:white;
  width: 100%;
  height:100%;
  position: fixed;
  overflow:hidden;
  left :0;
  top :0;
  font-size: 12px;
  z-index:109;
}

.msgtalking{
  // margin-bottom:45px;
  background: white;
  height:100%;
  width:100%;
  position:relative;
  // border-left:1px solid #ccc;
  // border-right:1px solid #ccc;
  .liao{
    width:100%;
    height:30px;
    line-height: 30px;
    font-size: 16px;
    background-image: linear-gradient(to top,#495a80,#495a80);
    color:white;
    text-align: center;
  }
  .main{
    width:100%;
    height:70%;
    text-align: center;
    p{
      margin-top:6px;
      color:#caa;
    }
    p.topp{
      margin-top:150px;
    }
  }
  .addtalk{
    width:100%;
    height:40px;
    background: #495a80;
    position:absolute;
    bottom:0px;
    left:0px;
    input{
      height:30px;
      outline:none;
      border:none;
      margin-top:4px;
      margin-left:5px;
      vertical-align: middle;
    }
    input.talktext{
      width:75%;
      padding-left:15px;
      border-radius: 2px;

    }
    input.talksubmit{
      width:15%;
      background:white;
      height:30px;
      border-radius: 2px;
    }



  }

}


</style>



