<template>
  <div class="communitycenter">
    <span class='spantop' @click="closeDetails">&lt;</span>
    <!-- 以下是具体内容 -->
    <div class="content">
        <!-- 第一个模块 -->
        <a href="http://jinan.dzwww.com/whcyw/whxx/201603/t20160321_14021891.htm" target='_blank' v-for='item in datas.communitycenterpic'>

        <!-- 以下是链接具体内容 -->
            <div class="conmodul1">
                <div class="msgs">
                    <div class="imgs">
                      <img :src="item.ptus" alt="">
                    </div>
                    <p class='texttitle'><span>【社区空间】</span>{{item.title}}</p>
                    <div class='morelink'><em>{{item.reads}}</em><span>阅读</span><i>查看更多>></i></div>
                </div>
            </div>

        </a>



    </div>

  </div>
</template>

<script>
import datas from '../../../datas.json'

export default {
  name:'communitycenter',
  data(){
    return{
      datas:datas
    }
  },
  methods:{
    closeDetails:function(){
      this.$emit('close')
    }
  }
}

</script>

<style lang="less" scoped>

div{
	box-sizing: border-box;
}
a{
  text-decoration: none;
  color:#fd5b78;
}
.communitycenter{
  width:100%;
  height:100%;
  position:fixed;
  top:0px;
  left:0px;
  background:white;
  z-index: 31;
  .spantop{
    position:absolute;
    left:10px;
    top:10px;
    width:25px;
    height:25px;
    text-align: center;
    line-height: 25px;;
    display:block;
    background: white;
    color:white;
    z-index: 81;
    color:black;
    border-radius: 50%;
    font-size: 20px;
  }
}

.content{
  width:100%;
  height:100%;
  // height:1800px;
  background: white;
  padding-left:5px;
  padding-right:5px;
  overflow:scroll;
  .conmodul1{
      margin-bottom:20px;
    width:100%;
    height:auto;
    padding-bottom:12px;
    margin-top:12px;
    background: white;
    border:1px solid #d1c7e2;
    // padding:10px 5px 0px 5px;
    border-radius: 3px;
    .imgs{
      width:100%;
      // height:160px;
      max-height:320px;
      img{
        width:100%;
        height:100%;
      }
    }
    p{
      margin-top:5px;
      padding-left:12px;
      padding-right:12px;
    }
    div.morelink{
      padding-left:12px;
      padding-right:12px;
      margin-top:12px;
      // color:red;
      font-size: 10px;
      padding-bottom:3px;
      em{
        margin-left:3px;
        color:black;
      }
      span{
        float:left;
        color:black;
      }
      i{
        float:right;
        color:#0d1017;
        // font-weight: bold;
      }
    }
    p.texttitle{
      margin-top:12px;
      font-size: 17px;
      span{
        color:black;
      }
    }
  }

}



</style>



