<template>
  <div class="release">

    <div class="header">
      <span class='topictitle'>{{datas.topheader[0].name}}</span>
      <span class='sharemoreprojects' @touchstart='showmoreprojects=!showmoreprojects' @touchend='selectProjecting1()'></span>


       <span class='login' @touchstart='showlogin=!showlogin' @touchend='selectProjecting2()'></span>


      <!-- <transition enter-active-class="animated rotateIn" leave-active-class="animated rotateOut">
            <span class='login' @touchstart='showlogin=!showlogin' @touchend='selectProjecting2()'></span>
      </transition> -->

    </div>
    <!-- 以下是发布中心  主体模块 -->
    <!-- @touchstart='selectProject()'  @touchend="closemoreprojects() -->
    <div class="releasecontents">
            <!-- 轮播图模块 -->
            <!-- <div class="slideshow">

            </div> -->

            <div class="swiper-container box">
                <div class="swiper-wrapper">
                    <div class="swiper-slide">
                        <img src="../../../static/img/a.png" alt="">
                    </div>
                    <div class="swiper-slide">
                        <img src="../../../static/img/legao01.jpg" alt="">
                    </div>
                    <div class="swiper-slide">
                        <img src="../../../static/img/wanjuche1.png" alt="">
                    </div>
                </div>
                <!-- 如果需要分页器 -->
                <div class="swiper-pagination"></div>

            	<!-- 如果需要导航按钮 -->
                <!-- <div class="swiper-button-prev"></div>
                <div class="swiper-button-next"></div> -->

                <!-- 如果需要滚动条 -->
                <!-- <div class="swiper-scrollbar"></div> -->

            </div>



            <!-- <p>{{datas.community[1]}}</p> -->
            <!-- 小模块部分 -->
            <div @touchend='selectProjecting3()' class="modulecontent" v-for='item in datas.community' @click="selectFood(item)">

              <div class='moduleborder'>
                <router-link to="/release">
                    <ul>

                        <li>
                          <i>【发起人】：{{item.originator}}</i>
                          <a href=""><span></span>距离你约{{item.distance}}里</a>
                        </li>
                        <li>
                          <p><i>【共享】</i><span>{{item.project}}</span></p>
                          <p class='detailtext'>【详情】：{{item.details}}</p>
                        </li>
                        <li>
                          <p><i class='iconfont'>&#xe627;</i><span>我要参与项目 | 点击众筹</span></p>
                        </li>
                        <li>
                          <p><span>地理位置：{{item.position}}</span> <i>发起时间：{{item.time}}</i></p>
                        </li>
                        <li>
                          <p><span>支持：{{item.supported}}人</span><i>收藏：{{item.collection}}人</i><em>项目截止时间:{{item.deadline}}</em></p>
                        </li>

                    </ul>
                    </router-link>
                </div>


            </div>

    </div>

    <!-- 以下是打开新页面的模块 -->
    <!-- 使用detail组件 -->
      <transition name="slide">
          <modul-detail v-show="showDetail" :item="currentDetail" @close="showDetail=false"></modul-detail>
      </transition>

      <!-- 使用发布项目组件 -->
        <transition name="moreprojects">
            <modul-moreprojects v-show="showmoreprojects"></modul-moreprojects>
        </transition>

      <!-- 使用登录注册组件 -->
        <transition name="login">
            <modul-login v-show="showlogin" :showlogins='showlogin'></modul-login>
        </transition>

  </div>
</template>

<script>
import datas from '../../../datas.json'
import Header from '../Header'
import moddetail from '../moddetail/moddetail'
import moreprojects from '../moreprojects/moreprojects'
import modullogin from '../modullogin/modullogin'

export default {
  name:'release',
  data(){
    return{
      datas:datas,
      showDetail : false,
      currentDetail : {},
      showmoreprojects:false,
      showlogin:false
    }
  },
  components : {
      'my-header':Header,
      'modul-detail':moddetail,
      'modul-moreprojects':moreprojects,
      'modul-login':modullogin
  },
  methods:{
    selectFood(item){
            this.showDetail = true;
            //将当前的food赋值给currentFood
            this.currentDetail = item;
        },
    selectProjecting1(){
          //判断：如果消息框已经打开，则关闭：控制相关数据
          if(this.showlogin){
            this.showlogin =false;
          }
        },
    selectProjecting2(){
          //判断：如果登录注册框已经打开，则关闭：控制相关数据
          if(this.showmoreprojects){
            this.showmoreprojects =false;
          }
        },
    selectProjecting3(){
          //重置关闭
          this.showmoreprojects =false;
          this.showlogin =false;
        }
  },
  mounted(){
    var mySwiper = new Swiper ('.swiper-container', {
    direction: 'vertical',
    loop: true,

    // 如果需要分页器
    pagination: '.swiper-pagination',

    // 如果需要前进后退按钮
    // nextButton: '.swiper-button-next',
    // prevButton: '.swiper-button-prev',

    // 如果需要滚动条
    // scrollbar: '.swiper-scrollbar',

  })


  }
}

</script>

<style lang='less' scoped>

.slide-enter-active,.slide-leave-active{
    transition: all .5s;
}
.slide-enter,.slide-leave-active {
    transform: translateX(100%);
    opacity: 0;
}
.moreprojects-enter-active,.moreprojects-leave-active{
    transition: all .5s;
}
.moreprojects-enter,.moreprojects-leave-active {
    transform: translateX(100%);
    opacity: 0;
}

.login-enter-active,.login-leave-active{
        transition: all .5s;
    }
.login-enter,.login-leave-active {
        transform: translateX(-100%);
        opacity: 0;
    }

.header{
  background:#495a80;
  height:45px;
  line-height: 45px;
  text-align: center;
  position:fixed;
  top:0px;
  left:0px;
  width:100%;
  z-index: 99;
  // color:white
  .topictitle{
    font-size: 20px;
    color:white;
  }
  .login{
    width:30px;
    height:30px;
    display:block;
    position: absolute;
    left:8px;
    top:8px;
    background-image:url('../../../static/img/login.png');
    background-position: left top;
    background-size:100% 100%;
    background-repeat: no-repeat;
  }
  .sharemoreprojects{
    width:30px;
    height:30px;
    display:block;
    position: absolute;
    right:12px;
    top:8px;
    background-image:url('../../../static/img/sharemoreprojects.png');
    background-position: left top;
    background-size:100% 100%;
    background-repeat: no-repeat;
  }

}
.releasecontents{
  margin-bottom:45px;
  margin-top:45px;
  // .slideshow{
  //   width:100%;
  //   height:140px;
  //   background: yellow;
  //   background-image:url('../../../static/img/a.png');
  //   background-size:100% 100%;
  //   background-position:left top;
  // }
  .box{
    width:100%;
    height:160px;
    // background: #cee;
    img{
      width:100%;
      height:160px;
    }
  }
}
div{
	box-sizing: border-box;
}

// 这个是嵌套模块，用于边框和背景
.moduleborder{
  padding:8px;
  box-shadow: 1px 1px 1px #c2c2c2;
}
a{
  color:black;
}

.modulecontent{
  width:100%;
  padding:6px;
  height:auto;
  margin-top:5px;
  margin-bottom:6px;
  ul>a{
    text-decoration: none;
    color:black;
  }
  a{
    text-decoration: none;

  }
  li:nth-of-type(1){
    width:100%;
    height:15px;
    line-height: 15px;
    i{
      font-size: 12px;
      margin-left:2px;
      color:gray;
    }
    a{
      color:red;
      font-size: 12px;
      float:right;
      margin-right:12px;
      position:relative;
      span{
        width:12px;
        height:12px;
        display:inline-block;
        background-image:url('../../../static/img/geofy.png');
        background-size:100% 100%;
        background-position:left top;
        position:absolute;
        left:-13px;
        top:1px;
      }
    }
  }
  li:nth-of-type(2){
    width:100%;
    height:auto;
    max-height:118px;
    line-height: 25px;
    padding:9px 0px 9px 0px;
    color:#3a352f;
    font-size: 15px;
    p{
      i{
        color:#eb3f2f;
      }
      span{
        color:#555555;
        font-weight: bold;
      }
    }
    p.detailtext{
      font-size: 8px;
      color:#c2c2c2;
      line-height: 9px;
    }
  }
  li:nth-of-type(3){
    width:100%;
    height:20px;
    margin-top:8px;
    margin-bottom:6px;
    p{
      float:right;
      span{
          margin-right:12px;
      }
      i{
        color:#fe3b73;
      }
    }
  }
  li:nth-of-type(4){
    width:100%;
    height:20px;
    line-height: 20px;
    // background: yellow;
    font-size: 8px;
    p{
      span{
        margin-left:12px;
      }
      i{
        font-style:normal;
        float:right;
        margin-right:20px;
      }
    }
  }
  li:nth-of-type(5){
    width:100%;
    height:28px;
    line-height: 28px;
    font-size: 12px;
    border-top:1px solid #ececec;
    span{
      margin-left:12px;
      margin-right:12px;
    }
    i{
      margin-right:12px;
    }
    // background: yellow;
  }

}



</style>



